'use client'
import React, { useState, useEffect } from 'react'
import './header.css'
import AppBtn from './AppBtn';
import Nav from './Nav'
import Image from 'next/image';
import logo from '../../../public/assets/hourg.jpg'

export default function Header() {
    const [scroll, setScroll] = useState(0);

    useEffect(() => {
        const handleScroll = () => setScroll(window.scrollY);

        window.addEventListener('scroll', handleScroll);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    return (
        <header
            id="header"
            className={`fixed-top d-flex align-items-center ${
                scroll > 100 ? 'header-scrolled' : ''
            }`}
        >
            <div className="container-fluid container-xl d-flex align-items-center justify-contents-lg-between">
                <h1 className="logo me-auto me-lg-0">
                    <a href="/">Time Capsule</a>
                </h1>
                <a href="index.html" className="logo me-auto me-lg-0">
                    <Image src={logo} alt="TC" className="img-fluid" />
                </a>
                <Nav />
                <AppBtn name="freeze your memory" />

            </div>
        </header>
    );
}
